'use strict';

var Emitter  = require('cjs-emitter'),
    parallel  = require('cjs-async/parallel'),

    sources = {},
    notificationStack = [],

    $widget = document.body.appendChild(document.createElement('div')),

    notify = function ( config ) {
        config.timeout = config.timeout || 5000;

        if ( config.buttons && config.buttons[0] && config.buttons[0].click ) {
            config.buttons[0].click();
            if ( Array.isArray(notificationStack) && notificationStack.length > 0 ) {
                notify(notificationStack.unshift());
            }
        } else {
            $widget.innerText = config.title;
            $widget.style.display = '';
            setTimeout(function () {
                $widget.style.display = 'none';
                if ( Array.isArray(notificationStack) && notificationStack.length > 0 ) {
                    notify(notificationStack.unshift());
                }
            }, config.timeout);
        }
    };


$widget.style.display = 'none';
$widget.style.textAlign = 'center';
$widget.style.background = '#000';
$widget.style.color = '#fff';
$widget.style.lineHeight = '72px';
$widget.style.height = 'auto';
$widget.style.position = 'absolute';
$widget.style.left = '0';
$widget.style.top = '0';
$widget.style.width = '100%';
$widget.style.zIndex = '9999';


function Core ( config ) {
    Emitter.call(this, config);

    this.plugins = {};

    this.applicationState = {
        focused: false,
        visible: false,
        home: false
    };
}

// inheritance
Core.prototype = Object.create(Emitter.prototype);
Core.prototype.constructor = Core;


Core.prototype.theme = {
    path: null
};

Core.prototype.environment = require('../system/environment');

Core.prototype.ready = false;

if ( !DEVELOP ) {
    Core.prototype.emit = function () {
        try {
            Emitter.prototype.emit.apply(this, arguments);
        } catch ( exception ) {
            if ( DEVELOP ) {
                debug('core')('ATTENTION! Exception in emit! ' + exception);
            }
        }
    };
}

