if ( PLATFORM === 'MAG' || NANOCORE ) {
    /* eslint new-cap: 0 */
    Core.prototype.apiVersion = parseInt(window.gSTB.Version().split(';')[0].split(':')[1], 10);
}


window.extendCorePrototype = function ( property, value ) {
    if ( DEVELOP ) {
        debug('kernel')('extendCorePrototype with ' + property);

        if ( property === 'backend' ) {
            value.requests = [];
            (function () {
                var originalRequest = value.request,
                    apiDebug = debug('backend-api-request');

                value.request = function ( url, options ) {
                    var eth0      = window.gSTB.GetLanLinkStatus(),
                        wifi      = window.gSTB.GetWifiLinkStatus(),
                        userAgent = 'Model: ' + window.gSTB.GetDeviceModelExt() + '; Link: ',
                        method    = options.method || 'GET',
                        request   = JSON.parse(JSON.stringify(options));


                    /* eslint no-nested-ternary: 0 */
                    request.method = method;
                    request.url = value.host + core.config.options.stalkerApiPath + url;

                    if ( request.headers ) {
                        request.headers['Authorization'] = 'Bearer ' + value.session.accessToken;
                        request.headers['Content-Type'] = 'application/json';
                        request.headers['Accept-Language'] = core.environment.language;
                        request.headers['X-User-Agent'] = userAgent + (eth0 && wifi ? 'Ethernet,WiFi' : wifi ? 'WiFi' : eth0 ? 'Ethernet' : '');
                    } else {
                        request.headers = {
                            Authorization: 'Bearer ' + value.session.accessToken,
                            'Content-Type': 'application/json',
                            'Accept-Language': core.environment.language,
                            'X-User-Agent': userAgent + (eth0 && wifi ? 'Ethernet,WiFi' : wifi ? 'WiFi' : eth0 ? 'Ethernet' : '')
                        };
                    }

                    //apiDebug('request: ' + method + ' ' + request.url);

                    //apiDebug('async: ' + request.async);

                    // remove properties with "undefined" values, so print incomplete information
                    //apiDebug('headers: ' + JSON.stringify(request.headers));

                    apiDebug(
                        'curl -X ' +
                        method + ' "' + request.url + '" -H ' +
                        Object.keys(request.headers).map(function ( key ) { return '"' + key + ': ' + request.headers[key] + '"'; }).join(' -H ') +
                        (options.body ? (' -d \'' + options.body + '\'') : '')
                    );

                    if ( options.type ) {
                        apiDebug('responseType: ' + options.type);
                    }

                    if ( options.timeout ) {
                        apiDebug('timeout: ' + options.timeout);
                    }

                    value.requests.push(request);

                    return originalRequest(url, options);
                };
            })();
        }
    }

    if ( property === 'notify' ) {
        notify = notificationStack = null;
        $widget.parentNode.removeChild($widget);
    }

    Core.prototype[property] = value;
};


module.exports = Core;
