try {
    environment = JSON.parse(environment);
    environment = environment.result;
} catch ( error ) {
    environment = {};
}

environment.language = environment.language || 'en';

environment.availableLanguages = [];

(function () {
    var index = 0,
        list = gSTB.RDir('tempfile read languages_utf8.txt').split('\n'),
        specificLanguages = {
            estoninan: {
                code: 'et',
                name: 'Estonian (Eesti keel)'
            },
            latvian: {
                code: 'lv',
                name: 'Latvian (Latvieu)'
            }
        },
        language, name;

    // YEAH! It's Legacy
    while ( index < list.length ) {
        if ( list[index] !== '' ) {
            /* eslint no-useless-escape: 0 */
            language = list[index].split('\"');
            // if ( language[1] === 'bg' || language[1] === 'es'  || language[1] === 'el' ) {
            //     name = language = language[1];
            // } else {
            name = decodeURIComponent(escape(language[3]));
            language = language[1];
            // }

            if ( language === specificLanguages.estoninan.code ) {
                specificLanguages.estoninan = false;
            }

            if ( language === specificLanguages.latvian.code ) {
                specificLanguages.latvian = true;
            }

            environment.availableLanguages.push({
                code: language,
                name: name
            });
        }
        ++index;
    }

    if ( specificLanguages.latvian ) {
        environment.availableLanguages.push(specificLanguages.latvian);
    }

    if ( specificLanguages.estoninan ) {
        environment.availableLanguages.push(specificLanguages.estoninan);
    }
})();

// set defaults if value empty
